import { Int1521FieldKeys } from "../integration/int15-2-1.field.rules";

/**
 * IC-27: INT15-2-1 -> INT19-2 Field Mapping
 * 
 * Source Root: payload
 * Destination Root: payload.request.request_body
 * 
 * INT15-2-1 receives a message from an EH topic to process a reservation for loyalty benefits.
 * INT19-2 will capture the relevant data required from this message and save it into Cloud DB.
 * 
 * IC Condition: Loyalty Stay & Earn - loyaltyCheckFailed
 * 
 * Field Mappings:
 * - LOY-1: confirmationNumber.value -> resConfirmationNumber (both always required)
 * - LOY-2: travelers[].altCustID -> acid (both always required)
 * - LOY-3: hotels[].propertyCode.code (field not used by SE, never present in destination)
 * - LOY-4 to LOY-7: Excluded
 */

export type Int192FieldKeys = keyof typeof import("../integration/int19-2.field.rules").INT192FieldRegexMap;

export const INT1521_TO_INT192_FieldPathMap: Record<string, string> = {
    'confirmationNumber.value': 'resConfirmationNumber',
    'travelers<array>.altCustID': 'acid',
};

