import { businessEventsClient, type CloudEvent } from '@dynatrace-sdk/client-classic-environment-v2';
import { SoxBusinessEvent, BusinessEventFromPairParams, BusinessEventFromSingleParam, SoxBusinessEventSingleInt } from './business-event.interface.js';
import { AnomalyCategory, AnomalyType } from './error-definitions.js';
import type {
  IntegrationValidationResult,
  PayloadRuleValidationResult,
  MappingComparisonResult,
  SingleIntegrationValidationResult
} from './integration-validation.types.js';

const MAX_FIELD_BYTES = 31 * 1024;

function byteLen(v: string): number {
  if (typeof Buffer !== 'undefined') return Buffer.byteLength(v, 'utf8');
  return new TextEncoder().encode(v).length;
}

function truncateUtf8(str: string | undefined, maxBytes = MAX_FIELD_BYTES): { value?: string; truncated: boolean } {
  if (!str) return { value: str, truncated: false };
  if (byteLen(str) <= maxBytes) return { value: str, truncated: false };
  const suffix = '...(truncated)';
  const suffixBytes = byteLen(suffix);
  const target = maxBytes - suffixBytes;
  let accBytes = 0;
  let out = '';
  for (const ch of str) {
    const chBytes = byteLen(ch);
    if (accBytes + chBytes > target) break;
    out += ch;
    accBytes += chBytes;
  }
  return { value: out + suffix, truncated: true };
}

export interface IngestResult {
  success: boolean;
  status?: number;
  error?: unknown;
  cloudEvent: CloudEvent;
  message: string;
  sourceDataTruncated?: boolean;
  destinationDataTruncated?: boolean;
}

function toEventDate(ts: string): Date {
  const d = new Date(ts);
  return isNaN(d.getTime()) ? new Date() : d;
}

/**
 * Build CloudEvent (now emits arrays errorTypes / errorSubTypes).
 */
export function toCloudEvent(sox: SoxBusinessEvent): {
  cloudEvent: CloudEvent;
  sourceDataTruncated: boolean;
  destinationDataTruncated: boolean;
} {
  const time = toEventDate(sox.timestamp);
  const { value: srcData, truncated: srcTrunc } = truncateUtf8(sox.sourceData);
  const { value: destData, truncated: destTrunc } = truncateUtf8(sox.destinationData);

  const cloudEvent = {
    specversion: '1.0',
    id: sox.eventId || crypto.randomUUID(),
    source: 'sox',
    type: sox.eventType,
    time,
    category: sox.eventType,
    provider: 'SOX',
    datacontenttype: 'application/json',
    data: {
      transactionId: sox.transactionId,
      sourceIntId: sox.sourceIntId,
      destIntId: sox.destIntId,
      srcEventTime: sox.srcEventTime,
      destEventTime: sox.destEventTime,
      anomalyCategory: sox.anomalyCategory,
      anomalyType: sox.anomalyType,
      anomalySummary: sox.anomalySummary,
      sourceData: srcData,
      destinationData: destData,
      sourceDataTruncated: srcTrunc ,
      destinationDataTruncated: destTrunc
    }
  } as CloudEvent;

  return { cloudEvent, sourceDataTruncated: srcTrunc, destinationDataTruncated: destTrunc };
}

export function toCloudEventSingleInt(sox: SoxBusinessEventSingleInt): {
  cloudEvent: CloudEvent;
  sourceDataTruncated: boolean;
} {
  const time = toEventDate(sox.timestamp);
  const { value: srcData, truncated: srcTrunc } = truncateUtf8(sox.sourceData);

  const cloudEvent = {
    specversion: '1.0',
    id: sox.eventId || crypto.randomUUID(),
    source: 'sox',
    type: sox.eventType,
    time,
    category: sox.eventType,
    provider: 'SOX',
    datacontenttype: 'application/json',
    data: {
      transactionId: sox.transactionId,
      sourceIntId: sox.sourceIntId,
      srcEventTime: sox.srcEventTime,
      anomalyCategory: sox.anomalyCategory as AnomalyCategory[] | undefined,
      anomalyType: sox.anomalyType as AnomalyType[] | undefined,
      anomalySummary: sox.anomalySummary,
      sourceData: srcData,
      sourceDataTruncated: srcTrunc || undefined,
    }
  } as CloudEvent;

  return { cloudEvent, sourceDataTruncated: srcTrunc};
}
export async function sendBusinessEvent(soxEvent: SoxBusinessEvent): Promise<IngestResult> {
  const { cloudEvent, sourceDataTruncated, destinationDataTruncated } = toCloudEvent(soxEvent);
  try {
    const resp = await businessEventsClient.ingest({
      body: cloudEvent,
      type: 'application/cloudevent+json'
    });
    const status = (resp as any)?.status ?? 200;
    return {
      success: true,
      status,
      cloudEvent,
      message: 'Business event ingested successfully',
      sourceDataTruncated,
      destinationDataTruncated
    };
  } catch (e: unknown) {
    return {
      success: false,
      cloudEvent,
      error: e,
      message: 'Failed to ingest business event',
      sourceDataTruncated,
      destinationDataTruncated
    };
  }
}

export async function sendBusinessEventSingleInt(soxEvent: SoxBusinessEventSingleInt): Promise<IngestResult> {
  const { cloudEvent, sourceDataTruncated } = toCloudEventSingleInt(soxEvent);
  try {
    const resp = await businessEventsClient.ingest({
      body: cloudEvent,
      type: 'application/cloudevent+json'
    });
    const status = (resp as any)?.status ?? 200;
    return {
      success: true,
      status,
      cloudEvent,
      message: 'Business event ingested successfully',
      sourceDataTruncated,
    };
  } catch (e: unknown) {
    return {
      success: false,
      cloudEvent,
      error: e,
      message: 'Failed to ingest business event',
      sourceDataTruncated,
    };
  }
}


/**
 * Returns arrays (may be empty) for errorTypes / errorSubTypes plus summary.
 */
function summarizeAnomalies(validation: IntegrationValidationResult|SingleIntegrationValidationResult, response: any): {
  anomalyCategory: AnomalyCategory[];
  anomalyType: AnomalyType[];
  anomalySummary?: string;
} {
  if (validation.isValid) return { anomalyCategory: [], anomalyType: [] };
  const collectFailures = (v?: PayloadRuleValidationResult|null) => v?.failures ?? [];
  const srcFailures = collectFailures(validation.sourceValidation);
  let destFailures;
  let allFailures;

  if('destinationValidation' in validation){
    destFailures = collectFailures(validation.destinationValidation);
    allFailures = [...srcFailures, ...destFailures];
  } else{
    allFailures = srcFailures;
    destFailures = [];
  }
  const mappingFailures = validation.mappingComparison?.mismatches ?? [];

  const categorySet = new Set<AnomalyCategory>();
  const typeSet = new Set<AnomalyType>();

  allFailures.forEach(f => {
    if (f.anomalyCategory) categorySet.add(f.anomalyCategory);
    if (f.anomalyType) typeSet.add(f.anomalyType);
  });
  mappingFailures.forEach(m => {
    if (m.anomalyCategory) categorySet.add(m.anomalyCategory);
    if (m.anomalyType) typeSet.add(m.anomalyType);
  });

  const anomalyCategory = [...categorySet];
  const anomalyType = [...typeSet];

  if (allFailures.length === 0 && mappingFailures.length === 0) {
    return { anomalyCategory, anomalyType };
  }

  const parts: string[] = [];

  const countIssues = `Src Failures:${srcFailures.length},Dest Failures: ${destFailures?.length},Map:${mappingFailures.length};`
  parts.push(countIssues);
  allFailures.forEach(f => {
    parts.push(
        `RULE|cat=${f.anomalyCategory}|type=${f.anomalyType}|path=${f.actualPath || ''}|value=${f.value}`
    );
  });

  mappingFailures.forEach(m => {
    parts.push(
        `MAP|cat=${m.anomalyCategory}|type=${m.anomalyType}|src=${m.sourcePath}|dest=${m.destinationPath}|srcVal=${m.sourceValue}|destVal=${m.destinationValue}|map=${m.mappedSourceRule}->${m.mappedDestinationRule}`
    );
  });

  if(allFailures.length === 1 && allFailures[0]?.anomalyCategory === 'Integration Failure'){
      parts.push(
        `RESPONSE|cat=${allFailures[0]?.anomalyCategory}|type=${allFailures[0]?.anomalyType}|responseValue=${JSON.stringify(response?.content?.response?.http_response_code)}`
      );
  }
  const anomalySummary = parts.join('; ');

  return { anomalyCategory, anomalyType, anomalySummary };
}

export async function createSoxBusinessEvent(params: BusinessEventFromPairParams|BusinessEventFromSingleParam):
  Promise<IngestResult> {
    let destEventTime: string | undefined;
    let destinationPayload: unknown;
    if ('destinationPayload' in params) {
      destinationPayload = params.destinationPayload;
    }
    if ('destEventTime' in params) {
      destEventTime = params.destEventTime;
    }
    const {
    validationResult,
    transactionId,
    srcEventTime,
    sourcePayload,
  } = params;  
  const { anomalyCategory, anomalyType, anomalySummary } = summarizeAnomalies(validationResult, sourcePayload);
  let sourceData: string | undefined;
  let destinationData: string | undefined;
  try { sourceData = JSON.stringify(sourcePayload) } catch { };
  try { destinationData = JSON.stringify(destinationPayload) } catch { };
    
  const businessEvent: SoxBusinessEvent = {
    timestamp: new Date().toISOString(),
    eventId: crypto.randomUUID(),
    eventProvider: 'SOX',
    eventType: validationResult.isValid ? 'OK' : 'ERROR',
    srcEventTime,
    destEventTime,
    transactionId,
    sourceIntId: validationResult.sourceIntegrationId,
    destIntId: validationResult.destinationIntegrationId,
    anomalyCategory: anomalyCategory ,
    anomalyType: anomalyType,
    anomalySummary,
    sourceData: sourceData,
    destinationData: destinationData
  };

  return sendBusinessEvent(businessEvent);
}