// Orchestrator to validate source & destination integration payloads and compare mapped field values.
import { createSoxBusinessEvent, type IngestResult } from './common/dynatrace.bizevents.js';
import { FIELD_RULES_REGISTRY } from './common/field-rules-registry.js';
import { resolveFieldPathMap } from './common/field-path-map.js';
import { Validators } from './common/validators.js';
import { IntegrationPairs, SingleIntegrations, INTEGRATIONS, IntegrationCode } from './common/integrations.js';
import { getReportAlarmData, AlarmDoc, DynatraceResult  }  from './reporting/anomaly-alarmdoc-data.js'; 
import { applyIntegrationPreprocessors } from './common/preprocessors.js';

// Shared types & wrapper registry (extracted)
import {
    WRAPPER_VALIDATOR_REGISTRY,
    type IntegrationValidationResult,
    type MappingComparisonResult,
    type PayloadRuleValidationResult,
    type SingleIntegrationValidationResult,
    type ValidateIntegrationParams,
    type ValidatePairParams
} from './common/integration-validation.types.js';

/**
 * Registry of integrationId -> field rule map.
 */

function parsePayloadContent(raw: unknown, label: string): any {
  if (typeof raw !== 'string') return {};
  try {
    return JSON.parse(raw);
  } catch (err) {
      console.error(`Failed to parse ${label}`, err);
    return {};
  }
}

/**
 * Validate a single integration (wrapper + rules).
 */
export function validateIntegration(params: ValidateIntegrationParams): SingleIntegrationValidationResult {
  const { sourceIntegrationId, payload } = params;
  const id = sourceIntegrationId?.toLowerCase();
  const errors: string[] = [];
  const content = payload.content;
  payload.content = parsePayloadContent(content, sourceIntegrationId);

  const wrapperValidator = WRAPPER_VALIDATOR_REGISTRY[id];

  if (wrapperValidator) {
    const w = wrapperValidator(payload);
    if (!w.isValid) w.errorMessages.forEach(e => errors.push(`Wrapper: ${e}`));
  }

  const rules = FIELD_RULES_REGISTRY[id];
  if (!rules) {
    errors.push(`Unsupported / unknown integration: ${id}`);
    return { sourceIntegrationId: id, sourceValidation: undefined, isValid: false, errors };
  }
  
  const sourceValidation = Validators.validatePayloadWithRules(rules, payload);
  if (!sourceValidation.isValid) errors.push(`Payload rule validation failed (${id})`);
  
  const isValid = errors.length === 0 && !!sourceValidation.isValid;
  return { sourceIntegrationId: id, sourceValidation, isValid, errors };
}

function isTrue(val: unknown): boolean {
    return val === true || (typeof val === 'string' && val.trim().toLowerCase() === 'true');
}

function skipOnLoyaltyValidationFailure(
    srcId: string,
    destId: string,
    destinationPayload: any
): boolean {
    const loyaltyCheckFailed = destinationPayload?.content?.payload?.loyaltyCheckFailed;

    // Accept boolean true or string 'true' (any casing/whitespace)
    const failedTrue =
        Validators._areValuesEqual(loyaltyCheckFailed, true) ||
        Validators._areValuesEqual(loyaltyCheckFailed, 'true');

    // Match supported source/destination pairs using INTEGRATIONS and case-insensitive compare
    const is15_1_1_to_19_1 =
        Validators._areValuesEqual(srcId, INTEGRATIONS.INT15_1_1) &&
        Validators._areValuesEqual(destId, INTEGRATIONS.INT19_1);

    const is15_2_1_to_19_2 =
        Validators._areValuesEqual(srcId, INTEGRATIONS.INT15_2_1) &&
        Validators._areValuesEqual(destId, INTEGRATIONS.INT19_2);
    
    const is15_3_1_to_19_3 =
        Validators._areValuesEqual(srcId, INTEGRATIONS.INT15_3_1) &&
        Validators._areValuesEqual(destId, INTEGRATIONS.INT19_3);

    return failedTrue && (is15_1_1_to_19_1 || is15_2_1_to_19_2 || is15_3_1_to_19_3);
}

/**
 * Validate integration pair (wrappers + rules + mapping comparison).
 */
export function validateIntegrationPair(params: ValidatePairParams): IntegrationValidationResult {
  let {
    sourceIntegrationId,
    destinationIntegrationId,
    sourcePayload,
    destinationPayload
  } = params as any;  
  
  const sourceContent = sourcePayload?.content;
  const destinationContent = destinationPayload?.content;
  sourcePayload.content = parsePayloadContent(sourceContent, 'source');
  destinationPayload.content = parsePayloadContent(destinationContent, 'destination');
  const srcId = sourceIntegrationId.toLowerCase();
  const destId = destinationIntegrationId.toLowerCase();
  const errors: string[] = [];

  const srcWrapperValidator = WRAPPER_VALIDATOR_REGISTRY[srcId];

  if (srcWrapperValidator) {
    const r = srcWrapperValidator(sourcePayload);
    if (!r.isValid) r.errorMessages.forEach(e => errors.push(`Source wrapper: ${e}`));
  }
  
  const destWrapperValidator = WRAPPER_VALIDATOR_REGISTRY[destId];
  if (destWrapperValidator) {
    const r = destWrapperValidator(destinationPayload);
    if (!r.isValid) r.errorMessages.forEach(e => errors.push(`Destination wrapper: ${e}`));
  }

  // Note: Per-integration preprocessors that require dataArr are applied in processMatchedPair

  const sourceRules = FIELD_RULES_REGISTRY[srcId];
  if (!sourceRules) errors.push(`Unsupported / unknown source integration: ${srcId}`);
  const destinationRules = FIELD_RULES_REGISTRY[destId];
  if (!destinationRules) errors.push(`Unsupported / unknown destination integration: ${destId}`);

  let sourceValidation: PayloadRuleValidationResult | undefined;
  let destinationValidation: PayloadRuleValidationResult | undefined;
  let mappingComparison: MappingComparisonResult | null = null;
  if (sourceRules) {
    sourceValidation = Validators.validatePayloadWithRules(sourceRules, sourcePayload);
    if (!sourceValidation.isValid) errors.push(`Source payload rule validation failed (${srcId})`);
  }

  if (skipOnLoyaltyValidationFailure(srcId, destId, destinationPayload))
    return {
      sourceIntegrationId: srcId,
      destinationIntegrationId: destId,
      sourceValidation: sourceValidation,
      destinationValidation: { isValid: true, errorMessages: [], failures: [] },
      isValid: true,
      errors: []
    };

  if (destinationRules) {
    destinationValidation = Validators.validatePayloadWithRules(destinationRules, destinationPayload);
    if (!destinationValidation.isValid) errors.push(`Destination payload rule validation failed (${destId})`);
  }

  if (sourceRules && destinationRules) {
    const fieldPathMap = resolveFieldPathMap(srcId, destId);
    if (!fieldPathMap) {
      errors.push(`No field path mapping available for ${srcId} -> ${destId}`);
    } else {
      mappingComparison = Validators.comparePayloadsWithFieldMap(
        fieldPathMap,
        sourcePayload,
        destinationPayload,
        sourceRules, 
        destinationRules,
      );
      if (!mappingComparison?.isValid) {
        errors.push(`Field value comparison failed for mapping ${srcId} -> ${destId}`, JSON.stringify(mappingComparison));
      }
    }
  }
  //console.log(`Validation result for ${srcId} -> ${destId}:`, { errors, sourceValidation, destinationValidation, mappingComparison });

  const isValid =
    errors.length === 0 &&
    !!sourceValidation?.isValid &&
    !!destinationValidation?.isValid &&
    (mappingComparison ? mappingComparison.isValid : true);

  return {
    sourceIntegrationId: srcId,
    destinationIntegrationId: destId,
    sourceValidation,
    destinationValidation,
    mappingComparison,
    isValid,
    errors
  };
}

/* ------------------------------------------------------------------------- */
/* processMatchedPair implementation (workflow-style helper)                */
/* ------------------------------------------------------------------------- */

export async function processMatchedPair({
  loopItemValue,
  srcIntegration,
  destIntegration
}: {
  loopItemValue: any;
  srcIntegration: string;
  destIntegration: string;
}): Promise<IngestResult> {
  if (!srcIntegration || !destIntegration) {
    throw new Error('processMatchedPair: srcIntegration and destIntegration are required.');
  }

  const dataArr = Array.isArray(loopItemValue?.data) ? loopItemValue.data : [];
  if (dataArr.length === 0) {
    throw new Error('processMatchedPair: loopItemValue.data must be a non-empty array.');
  }

  const srcKey = String(srcIntegration).toLowerCase();
  const destKey = String(destIntegration).toLowerCase();

  // Default selection (first matching source/destination)
  let sourcePayload = dataArr.find(p =>
    p?.sox_integration && String(p.sox_integration).toLowerCase() === srcKey
  );
  let destinationPayload = dataArr.find(p =>
    p?.sox_integration && String(p.sox_integration).toLowerCase() === destKey
  );

  const pre = applyIntegrationPreprocessors(srcKey, destKey, dataArr);
  sourcePayload = pre.sourcePayload ?? sourcePayload;
  destinationPayload = pre.destinationPayload ?? destinationPayload;

  if (!sourcePayload) {
    throw new Error(`processMatchedPair: No payload found for srcIntegration='${srcIntegration}'`);
  }
  if (!destinationPayload) {
    throw new Error(`processMatchedPair: No payload found for destIntegration='${destIntegration}'`);
  }

  const sourceIntegrationId = sourcePayload.sox_integration;
  const destinationIntegrationId = destinationPayload.sox_integration;
  const srcEventTime = sourcePayload?.sox_transaction_timestamp || new Date().toISOString();
  const destEventTime = destinationPayload?.sox_transaction_timestamp || srcEventTime;
  const transactionId =
    loopItemValue?.sox_transaction_id ||
    sourcePayload?.sox_transaction_id ||
    destinationPayload?.sox_transaction_id ||
    crypto.randomUUID();

  const validationResult = validateIntegrationPair({
    sourceIntegrationId,
    destinationIntegrationId,
    sourcePayload,
    destinationPayload
  });

    console.log(
        'processMatchedPair summary:',
        JSON.stringify({
            sourceIntegrationId: validationResult.sourceIntegrationId,
            destinationIntegrationId: validationResult.destinationIntegrationId,
            sourceErrorCount: validationResult.sourceValidation?.failures.length || 0,
            destinationErrorCount: validationResult.destinationValidation?.failures.length || 0,
            mappingErrorCount: validationResult.mappingComparison?.mismatches.length || 0
        }));

  const ingestResult = await createSoxBusinessEvent({
    validationResult,
    transactionId,
    srcEventTime,
    destEventTime,
    sourcePayload,
    destinationPayload
  });

 // console.log('processMatchedPair ingestResult:', ingestResult);
  return ingestResult;
}

/* ------------------------------------------------------------------------- */
/* processSingleIntegration implementation (single log workflow helper)     */
/* ------------------------------------------------------------------------- */

export async function processSingleIntegration({ loopItemValue }: { loopItemValue: any }): Promise<IngestResult> {
    
    const soxData = loopItemValue.data[0]        

    if (typeof soxData !== 'object') {
        throw new Error('processSingleIntegration: no valid content found (soxData = loopItemValue?.data[0]): ' + JSON.stringify(soxData));
    }

    const sourceIntegrationId = soxData.sox_integration;
    const srcEventTime = soxData.sox_transaction_timestamp || new Date().toISOString();
    const transactionId = soxData.sox_transaction_id
        

    const validationResult = validateIntegration({
        sourceIntegrationId,
        payload: soxData
    });

  //  console.log('processSingleIntegration validationResult:', validationResult);

    const ingestResult = await createSoxBusinessEvent({
        validationResult,
        transactionId,
        srcEventTime,
        sourcePayload: soxData
    });

    //console.log('processSingleIntegration ingestResult:', ingestResult);
    return ingestResult;
}

/* ------------------------------------------------------------------------- */
/* processMissingTransaction (explicit failure handling helper)             */
/* ------------------------------------------------------------------------- */

export async function processMissingTransaction({ loopItemValue }: { loopItemValue: any }): Promise<IngestResult> {
    // Treat same as single integration; validator will mark failure if content.success == 0.
    const payload =
        (Array.isArray(loopItemValue?.data) && loopItemValue.data.length > 0
            ? loopItemValue.data[0]
            : loopItemValue?.payload) || loopItemValue;

    if (!payload || typeof payload !== 'object') {
        throw new Error('processMissingTransaction: no valid failure payload found (expected object).');
    }

    const sourceIntegrationId = payload.sox_integration;
    const srcEventTime = payload.sox_transaction_timestamp || new Date().toISOString();
    const transactionId =
        loopItemValue?.sox_transaction_id ||
        payload.sox_transaction_id ||
        crypto.randomUUID();

    const validationResult = validateIntegration({
        sourceIntegrationId,
        payload
    });

    if (!validationResult.sourceValidation)
        validationResult.sourceValidation = { isValid: false, errorMessages: [], failures: [] }

    validationResult.isValid = false; // Force overall invalid
    const failures = validationResult.sourceValidation?.failures;
    failures.push({
        rulePath: '',
        actualPath: '',
        value: payload?.content?.success,
        anomalyCategory: "Missing Transaction",
        anomalyType: "Missing Transaction Pair"
    });

    const ingestResult = await createSoxBusinessEvent({
        validationResult,
        transactionId,
        srcEventTime,
        sourcePayload: payload
    });
   // console.log('processIntegrationFailure ingestResult:', ingestResult);
    return ingestResult;
}

export function processReportData(dtResult: DynatraceResult, dynatraceDashboardUrl: string): AlarmDoc[]{
    return getReportAlarmData(dtResult, dynatraceDashboardUrl);
};



/* ------------------------------------------------------------------------- */
/**
 * Default export aggregates public API (keep in sync with named exports).
 */
export default {
  validateIntegration,
  validateIntegrationPair,
  createSoxBusinessEvent,
  processMatchedPair,
  processSingleIntegration,
  processMissingTransaction,
  processReportData,
  IntegrationPairs, SingleIntegrations,
};
