import {
  type FieldPathMap,
} from './integration-validation.types.js';
import { INT031_TO_INT04_FieldPathMap } from '../integration-pair/source.int03-1.dest.int04.map.rules.js';
import { INT032_TO_INT04_FieldPathMap } from '../integration-pair/source.int03-2.dest.int04.map.rules.js';
import { INT04_TO_INT31_FieldPathMap } from '../integration-pair/source.int04.dest.int31.map.rules.js';
import { INT04_TO_INT1511_FieldPathMap } from '../integration-pair/source.int04.dest.int1511.map.rules.js';
