import { INT1511_TO_INT191_FieldPathMap } from '../integration-pair/source.int15-1-1.dest.int19-1.map.rules.js';
import { INT1521_TO_INT192_FieldPathMap } from '../integration-pair/source.int15-2-1.dest.int19-2.map.rules.js';
import { INT1522_TO_INT521_FieldPathMap } from '../integration-pair/source.int15-2-2.dest.int15-2-1.map.rules.js';
import { INT1522_TO_INT241_FieldPathMap } from '../integration-pair/source.int15-2-2.dest.int24-1.map.rules.js';
import { INT1531_TO_INT193_FieldPathMap } from '../integration-pair/source.int15-3-1.dest.int19-3.map.rules.js';
import { INT1532_TO_INT1531_FieldPathMap } from '../integration-pair/source.int15-3-2.dest.int15-3-1.map.rules.js';
import { INT16_TO_INT17_FieldPathMap } from '../integration-pair/source.int16.dest.int17.map.rules.js';
import { INT16_TO_INT20_FieldPathMap } from '../integration-pair/source.int16.dest.int20.map.rules.js';
import { INT17_TO_INT18_FieldPathMap } from '../integration-pair/source.int17.dest.int18.map.rules.js';
import { INT191_TO_INT16_FieldPathMap } from '../integration-pair/source.int19-1.dest.int16.map.rules.js';
import { INT192_TO_INT16_FieldPathMap } from '../integration-pair/source.int19-2.dest.int16.map.rules.js';
import { INT21_TO_INT22_FieldPathMap } from '../integration-pair/source.int21.dest.int22.map.rules.js';
