import { INT26_TO_INT30_FieldPathMap } from '../integration-pair/source.int26.dest.int30.map.rules.js';
import { INT27_TO_INT28_FieldPathMap } from '../integration-pair/source.int27.dest.int28.map.rules.js';
import { INT28_TO_INT29_FieldPathMap } from '../integration-pair/source.int28.dest.int29.map.rules.js';
import { INT322_TO_INT321_FieldPathMap } from '../integration-pair/source.int32-2.dest.int32-1.map.rules.js';
import { INT332_TO_INT331_FieldPathMap } from '../integration-pair/source.int33-2.dest.int33-1.map.rules.js';



export function resolveFieldPathMap(source: string, dest: string): FieldPathMap | null {
  if (source === 'int15-3-2' && dest === 'int15-3-1') {
    return INT1532_TO_INT1531_FieldPathMap;
  } 

  if (source === 'int33-2' && dest === 'int33-1') {
    return INT332_TO_INT331_FieldPathMap;
  } 
  if(source === 'int32-2' && dest === 'int32-1'){
    return INT322_TO_INT321_FieldPathMap;
  }
  if(source === 'int16' && dest === 'int17'){
    return INT16_TO_INT17_FieldPathMap
  }
  if(source === 'int17' && dest === 'int18'){
    return INT17_TO_INT18_FieldPathMap;
  }
  if(source === 'int19-2' && dest === 'int16'){
    return INT192_TO_INT16_FieldPathMap;
  }
  if(source === 'int19-1' && dest === 'int16'){
    return INT191_TO_INT16_FieldPathMap;
  }
  if(source === 'int03-2' && dest === 'int04'){
    return INT032_TO_INT04_FieldPathMap;
  }
  if(source === 'int03-1' && dest === 'int04'){
    return INT031_TO_INT04_FieldPathMap;
  }
  if(source === 'int04' && dest === 'int31'){
    return INT04_TO_INT31_FieldPathMap;
  }
  if(source === 'int12-2' && dest === 'int12-1'){
    return INT122_TO_INT121_FieldPathMap;
  }
  if(source === 'int04' && dest === 'int15-1-1'){
    return INT04_TO_INT1511_FieldPathMap;
  }
  if(source === 'int16' && dest === 'int20'){
    return INT16_TO_INT20_FieldPathMap;
  }
  if(source === 'int27' && dest === 'int28'){
    return INT27_TO_INT28_FieldPathMap;
  }
  if(source === 'int28' && dest === 'int29'){
    return INT28_TO_INT29_FieldPathMap;
  }
  if(source === 'int11-2' && dest === 'int11'){
    return INT112_TO_INT11_FieldPathMap;
  }
  if(source === 'int15-2-2' && dest === 'int15-2-1'){
    return INT1522_TO_INT521_FieldPathMap;
  }
  if(source === 'int26' && dest === 'int30'){
    return INT26_TO_INT30_FieldPathMap;
  }
  if(source === 'int25' && dest === 'int26'){
    return INT25_TO_INT26_FieldPathMap;
  }
  if(source === 'int15-2-2' && dest === 'int24-1'){
    return INT1522_TO_INT241_FieldPathMap;
  }  
  if(source === 'int15-3-1' && dest === 'int19-3'){
    return INT1531_TO_INT193_FieldPathMap;
  }
  if(source === 'int15-2-1' && dest === 'int19-2'){
    return INT1521_TO_INT192_FieldPathMap;
  }
  if(source === 'int15-1-1' && dest === 'int19-1'){
    return INT1511_TO_INT191_FieldPathMap;
  }

  if(source === 'int21' && dest === 'int22'){
    return INT21_TO_INT22_FieldPathMap;
  }
  return null;
} 