import { REGEX } from '../common/regex-constants.js';
import type { RuleMap } from '../common/integration-validation.types.js';

// INT21 (Order Orchestrator → Settlement) test field rules
// Notes:
// - LOY-1: confirmationNumber — INT21 logs the Process Redemption call. In analysis,
//   we compare INT21 request to INT22 response; this file retains existing INT21 test path.
// - LOY-2: memberAccountNumber — INT21 tracks ACID; not mapped to INT22 CSID (destination-only).
// - LOY-3: propertyCode — required in both INT21 and INT22.
export const INT21FieldRegexMap:RuleMap = {
  // LOY-1 (test path retained): confirmation number
  'request.request_body.redemptionReservations<array>.confirmationNumber': {regex: REGEX.ALPHANUMERIC, optional: true },
  // LOY-2 (destination-only in comparison)
  'request.request_body.memberAccount.memberAccountNumber': REGEX.ALPHANUMERIC,
  // LOY-3: property code required
  'request.request_body.propertyCode': REGEX.ALPHANUMERIC
};
