import { REGEX } from '../common/regex-constants.js';
import type { RuleMap } from '../common/integration-validation.types.js';

// INT22 (Settlement Service) test field rules
// Notes:
// - LOY-1: confirmationNumber — optional overall because Cancel flow lacks it;
//   present for Create/Modify. We validate request path here; mapping uses response path.
// - LOY-2: memberAccountNumber — destination-only in comparison; leave optional.
// - LOY-3: propertyCode — required.
export const INT22FieldRegexMap:RuleMap = {
  // LOY-1: confirmation number may be absent for Cancel operations
  'request.request_body.redemptionReservations[0].confirmationNumber': {regex: REGEX.ALPHANUMERIC, optional: true},
  // LOY-2: member account number optional
  'request.request_body.memberAccount.memberAccountNumber': REGEX.ALPHANUMERIC,
  // LOY-3: property code required
  'request.request_body.propertyCode': REGEX.ALPHANUMERIC
};