import { Int1511FieldKeys } from "../integration/int15-1-1.field.rules";

/**
 * IC-26: INT15-1-1 -> INT19-1 Field Mapping
 * 
 * Source Root: payload
 * Destination Root: payload
 * 
 * INT15-1-1 receives a message from an EH topic to process a folio for loyalty benefits.
 * INT19-1 will capture the relevant data required from this message and save it into Cloud DB.
 * 
 * IC Condition: Loyalty Stay & Earn - loyaltyCheckFailed
 * 
 * Field Mappings:
 * - LOY-1: confirmationIds[].value -> resConfirmationNumber (both always required)
 *          Compare confirmationIds.value from source when confirmationIds.provider = "ACRS" 
 *          to the reservationConfirmationNumber in destination.
 * - LOY-2: customerId -> acid (both always required)
 *          SE confirmed that customerId in source is the same thing as acid in destination.
 * - LOY-3 to LOY-7: Excluded
 */

export type Int191FieldKeys = keyof typeof import("../integration/int19-1.field.rules").INT191FieldRegexMap;

export const INT1511_TO_INT191_FieldPathMap: Partial<Record<Int1511FieldKeys, Int191FieldKeys>> = {
    'confirmationIds<array>.value': 'resConfirmationNumber',
    'customerId': 'acid',
};

