// src/common/validators.ts

import type { AnomalyCategory, AnomalyType } from './error-definitions.js';
import type {
  RuleDefinition,
  ExtendedRuleObject,
  RuleOverrideExpression,
} from './integration-validation.types.js';

export const Validators = {
    isEmpty(value: Object) {
        if (value == null) return true;
        if (typeof value === 'string') return value.trim().length === 0;
        if (Array.isArray(value)) return value.length === 0;
        if (typeof value === 'object') return Object.keys(value).length === 0;
        return false;
    },

    /**
     * Case-insensitive & structural equality used by mapping comparison.
     * - Primitives compared directly
     * - Strings compared case-insensitive (locale-insensitive lowercasing)
     * - Objects/arrays compared via JSON structural serialization
     */
    _areValuesEqual(a: unknown, b: unknown): boolean {
        if (a === b) return true;
        // Normalize numeric-like values to 2 decimals and compare
        const toFixed2IfNumeric = (v: unknown): string | null => {
            if (typeof v === 'number' && Number.isFinite(v)) {
                return v.toFixed(2);
            }
            if (typeof v === 'string') {
                const s = v.trim();
                if (/^-?\d+(\.\d+)?$/.test(s)) {
                    const n = Number(s);
                    if (Number.isFinite(n)) return n.toFixed(2);
                }
            }
            return null;
        };
        const a2 = toFixed2IfNumeric(a);
        const b2 = toFixed2IfNumeric(b);
        if (a2 !== null && b2 !== null) {
            return a2 === b2;
        }
        // Case-insensitive string comparison
        if (typeof a === 'string' && typeof b === 'string') {
            return a.trim().toLowerCase() === b.trim().toLowerCase();
        }
        // Structural comparison for objects/arrays
        if (a && b && typeof a === 'object' && typeof b === 'object') {
            try {
                return JSON.stringify(a) === JSON.stringify(b);
            } catch {
                return false;
            }
        }
        return false;
    },

    /**
     * Async SOX wrapper structural validation.
     */
    validateAsyncSoxWrapper(value: unknown): { isValid: boolean; errorMessages: string[] } {
        const errors: string[] = [];
        const isNonEmptyString = (v: unknown) => typeof v === 'string' && v.trim().length > 0;

        if (value == null || typeof value !== 'object' || Array.isArray(value)) {
            errors.push('Wrapper must be a non-null object');
            return { isValid: false, errorMessages: errors };
        }

        const {
            sox_integration,
            sox_transaction_timestamp,
            sox_transaction_id,
            content
        } = value as any;

        if (!isNonEmptyString(sox_integration)) errors.push('Missing or invalid: sox_integration');
        if (!isNonEmptyString(sox_transaction_timestamp)) errors.push('Missing or invalid: sox_transaction_timestamp');
        if (!isNonEmptyString(sox_transaction_id)) errors.push('Missing or invalid: sox_transaction_id');

        if (content == null || typeof content !== 'object' || Array.isArray(content)) {
            errors.push('Missing or invalid: content');
        } else {
            const { success } = content as any;
            const successNumber = Number(success);
            const isFiniteNumber = isFinite(successNumber);
            const isValidSuccess = successNumber === 0 || successNumber === 1;
            if (!isFiniteNumber && !isValidSuccess) {
                errors.push(`Missing or invalid: content.success expected value 0 or 1, actual value: ${content.success}`);
            }
        }

        return { isValid: errors.length === 0, errorMessages: errors };
    },

    /**
     * HTTP SOX wrapper structural validation.
     */
    validateHttpSoxWrapper(value: unknown): { isValid: boolean; errorMessages: string[]} {
        const errors: string[] = [];
        const isNonEmptyString = (v: unknown) => typeof v === 'string' && v.trim().length > 0;

        if (value == null || typeof value !== 'object' || Array.isArray(value)) {
            errors.push('Wrapper must be a non-null object');
            return { isValid: false, errorMessages: errors };
        }
        const w: any = value;
        if (!isNonEmptyString(w.sox_integration)) errors.push('Missing or invalid: sox_integration');
        if (!isNonEmptyString(w.sox_transaction_id)) errors.push('Missing or invalid: sox_transaction_id');
        if (!isNonEmptyString(w.sox_transaction_timestamp)) errors.push('Missing or invalid: sox_transaction_timestamp');
        // if (!isNonEmptyString(w['@timestamp'])) errors.push('Missing or invalid: @timestamp');

        if (w.content == null || typeof w.content !== 'object' || Array.isArray(w.content)) {
            errors.push('Missing or invalid: content');
            return { isValid: false, errorMessages: errors };
        }
        const { response } = w.content;
        if (response == null || typeof response !== 'object' || Array.isArray(response)) {
            errors.push('Missing or invalid: content.response');
        } else {
            const httpCode = response.http_response_code.slice(0,3);
            if (!/^\d{3}$/.test(httpCode ?? '')) {
                errors.push('Missing or invalid: content.response.http_response_code (must be 3 digits)');
            }
        }

        return { isValid: errors.length === 0, errorMessages: errors };
    },

    _extractBusinessPayload(input: unknown): unknown {
        if (!input || typeof input !== 'object' || Array.isArray(input)) return input;

        const w: any = input;
        
        if (w.content && typeof w.content === 'object') {
            let candidate = w.content.payload;
            if (candidate && typeof candidate === 'object') {
                if (!Array.isArray(candidate) && candidate.payload && typeof candidate.payload === 'object') {
                    candidate = candidate.payload;
                }
                return candidate;
            } else{
                return w.content;
            }
        }
        return input;
    },

    _flattenToPathValueMap(root: any): Record<string, unknown> {
        const out: Record<string, unknown> = {};
        const walk = (val: any, path: string) => {
            if (val === null || val === undefined) {
                out[path] = val;
                return;
            }
            if (Array.isArray(val)) {
                if (val.length === 0) {
                    out[path] = val;
                    return;
                }
                val.forEach((item, idx) => {
                    const p = path ? `${path}<array${idx + 1}>` : `<array${idx + 1}>`;
                    walk(item, p);
                });
                return;
            }
            if (typeof val === 'object') {
                const keys = Object.keys(val);
                if (keys.length === 0) {
                    out[path] = val;
                    return;
                }
                keys.forEach(k => {
                    const p = path ? `${path}.${k}` : k;
                    walk(val[k], p);
                });
                return;
            }
            out[path] = val;
        };
        walk(root, '');
        return out;
    },

    _checkAllArrayValuesPresent(
        payload: Record<string, any>,
        path: string
    ): {
        isValid: boolean;
        path: string;
    } {
        const [variableName, requiredField] = path.split('<array>.');

        const keys = Object.keys(payload);
        const arrayMap: Record<string, Set<string>> = {};

        keys.forEach(key => {
            const match = key.match(new RegExp(`^${variableName}<array(\\d+)>\\.(\\w+)$`));
            if (match) {
                const [, index, field] = match;
                const groupKey = `${variableName}<array${index}>`;
                if (!arrayMap[groupKey]) arrayMap[groupKey] = new Set();
                arrayMap[groupKey].add(field);
            }
        });

        let isValid = true;

        for (const groupKey of Object.keys(arrayMap)) {
            const fullKey = `${groupKey}.${requiredField}`;
            const value = payload[fullKey];

            if (value === undefined || value === null || value === '') {
                console.log(`${fullKey} is missing or empty`);
                isValid = false;
            }
        }

        return { isValid, path };
    },

    validatePayloadWithRules(
        ruleMap: Record<string, RuleDefinition>,
        payload: any,
    ): {
        isValid: boolean;
        errorMessages: string[];
        failures: {
            rulePath: string; actualPath: string; value: unknown;
            anomalyCategory: AnomalyCategory, anomalyType: AnomalyType
        }[];
    } {
        const errorMessages: string[] = [];
        const failures: { rulePath: string; actualPath: string; value: unknown; anomalyCategory: AnomalyCategory, anomalyType: AnomalyType }[] = [];
        payload = payload?.content?.payload ?? payload?.content; 

        let isSuccess = (payload?.success === 0 || payload?.success === "0") ? false : true;
  
        if(!isSuccess){
            errorMessages.push(payload.response?.response_error_message);
            failures.push({
                        rulePath:'', actualPath:'' , value: payload.response?.http_response_code, anomalyCategory: 'Integration Failure',
                        anomalyType: 'Unsuccessful Response'
                    });
        }

        payload = this._extractBusinessPayload(payload);
        if (payload == null || typeof payload !== 'object' || Array.isArray(payload)) {
            return {
                isValid: false,
                errorMessages: ['Payload must be a non-null object'],
                failures: [{
                    rulePath: '*',
                    actualPath: '',
                    value: payload,
                    anomalyCategory: 'Missing Metadata',
                    anomalyType: 'Missing SOX data'
                }]
            };
        }

        const flat = this._flattenToPathValueMap(payload);

        const normalize = (p: string) =>
            p.replace(/<array\d+>/g, '<array>').replace(/^\./, '');
        
        for (const [rulePath, rx] of Object.entries(ruleMap)) {
            const isRequired = rx instanceof RegExp || !rx?.optional;
            const matching = Object.entries(flat).filter(([actual]) => {
                return normalize(actual) === rulePath;
            });
            if (matching.length === 0 && isRequired) {
                errorMessages.push(`Missing field: ${rulePath}`);
                failures.push({
                    rulePath, actualPath: rulePath, value: undefined,
                    anomalyCategory: 'Field Level Anomaly', anomalyType: 'Missing Field'
                });
                errorMessages.push(`Missing value: ${rulePath}`);
                failures.push({
                    rulePath, actualPath: rulePath, value: undefined,
                    anomalyCategory: 'Field Level Anomaly', anomalyType: 'Missing Value'
                });

                continue;
            } 
            // else if(isRequired && rulePath.indexOf('array') > -1){
            //     const pathSplit = rulePath.split('.');
            //     // const value = pathSplit[pathSplit.length - 1];
            //     const {isValid} = this._checkAllArrayValuesPresent(flat, rulePath);

            //     if(!isValid){
            //         errorMessages.push(`Missing value: ${rulePath}`);
            //         failures.push({
            //             rulePath, actualPath: rulePath, value: undefined,
            //             anomalyCategory: 'Field Level Anomaly', anomalyType: 'Missing Value'
            //         });
            //         continue;
            //     }
            // }
            
            matching.forEach(([actualPath, value]) => {
                if (this.isEmpty(value as any)  && isRequired) {
                    errorMessages.push(`Empty value at ${actualPath} (rule: ${rulePath})`);
                    failures.push({
                        rulePath, actualPath, value, anomalyCategory: 'Field Level Anomaly',
                        anomalyType: 'Missing Value'
                    });
                } else if (!(rx instanceof RegExp) && rx.regex && !rx.optional && !rx.regex.test(String(value))) {
                    errorMessages.push(`Invalid format at ${actualPath} value="${value}" (rule: ${rulePath})`);
                    failures.push({
                        rulePath, actualPath, value, anomalyCategory: 'Field Level Anomaly',
                        anomalyType: 'Invalid Field Format'
                    });
                } else if (rx instanceof RegExp && !rx.test(String(value))) {
                    errorMessages.push(`Invalid format at ${actualPath} value="${value}" (rule: ${rulePath})`);
                    failures.push({
                        rulePath, actualPath, value, anomalyCategory: 'Field Level Anomaly',
                        anomalyType: 'Invalid Field Format'
                    });
                }
            });
        }

        return {
            isValid: errorMessages.length === 0,
            errorMessages,
            failures
        };
    },

    comparePayloadsWithFieldMap(
        fieldPathMap: Record<string, string>,
        sourcePayload: unknown,
        destinationPayload: unknown,
        sourceRules:any,
        destRules: any,
    ): {
        isValid: boolean;
        errorMessages: string[];
        missingSource: string[];
        missingDestination: string[];
        mismatches: {
            sourcePath: string;
            destinationPath: string;
            mappedSourceRule: string;
            mappedDestinationRule: string;
            sourceValue: unknown;
            destinationValue: unknown;
            anomalyCategory: AnomalyCategory;
            anomalyType: AnomalyType;
        }[];
    } {
        const errorMessages: string[] = [];
        const missingSource: string[] = [];
        const missingDestination: string[] = [];
        const mismatches: {
            sourcePath: string;
            destinationPath: string;
            mappedSourceRule: string;
            mappedDestinationRule: string;
            sourceValue: unknown;
            destinationValue: unknown;
            anomalyCategory: AnomalyCategory;
            anomalyType: AnomalyType;
        }[] = [];

        const normalize = (p: string) =>
            p.replace(/<array\d+>/g, '<array>').replace(/^\./, '');

        if (sourcePayload == null || typeof sourcePayload !== 'object' || Array.isArray(sourcePayload)) {
            return {
                isValid: false,
                errorMessages: ['Source payload invalid (must be non-null object)'],
                missingSource,
                missingDestination,
                mismatches
            };
        }
        if (destinationPayload == null || typeof destinationPayload !== 'object' || Array.isArray(destinationPayload)) {
            return {
                isValid: false,
                errorMessages: ['Destination payload invalid (must be non-null object)'],
                missingSource,
                missingDestination,
                mismatches
            };
        }

        const sourcePayloadExtracted = this._extractBusinessPayload(sourcePayload);
        const destinationPayloadExtracted = this._extractBusinessPayload(destinationPayload);

        const flatSource = this._flattenToPathValueMap(sourcePayloadExtracted);
        const flatDest = this._flattenToPathValueMap(destinationPayloadExtracted);

        const groupByNormalized = (flat: Record<string, unknown>) => {
            const map: Record<string, { rawPath: string; value: unknown }[]> = {};
            Object.entries(flat).forEach(([raw, val]) => {
                const norm = normalize(raw);
                if (!map[norm]) map[norm] = [];
                map[norm].push({ rawPath: raw, value: val });
            });
            return map;
        };

        const srcGrouped = groupByNormalized(flatSource);
        const destGrouped = groupByNormalized(flatDest);
        for (const [srcRule, destRule] of Object.entries(fieldPathMap)) {
            const srcEntries = srcGrouped[srcRule] || [];
            const destEntries = destGrouped[destRule] || [];
            const srcRequired = !sourceRules[srcRule]?.optional;
            const destRequired = !destRules[destRule]?.optional;

            if (srcEntries.length === 0 && srcRequired) {
                missingSource.push(srcRule);
                errorMessages.push(`Missing source field(s) for rule: ${srcRule}`);
                continue;
            }
            if (destEntries.length === 0 && destRequired) {
                missingDestination.push(destRule);
                errorMessages.push(`Missing destination field(s) for rule: ${destRule}`);
                continue;
            }

            if (srcRequired && destRequired && (srcEntries.length !== destEntries.length)) {
                errorMessages.push(`Count mismatch for mapping ${srcRule} -> ${destRule} (source=${srcEntries.length}, dest=${destEntries.length})`);
                mismatches.push({
                    sourcePath: srcEntries.map(e => e.rawPath).join(','),
                    destinationPath: destEntries.map(e => e.rawPath).join(','),
                    mappedSourceRule: srcRule,
                    mappedDestinationRule: destRule,
                    sourceValue: srcEntries.map(e => e.value),
                    destinationValue: destEntries.map(e => e.value),
                    anomalyCategory: 'Field Level Anomaly',
                    anomalyType: 'Missing Value'
                });
            }

            const pairCount = Math.min(srcEntries.length, destEntries.length);
            for (let i = 0; i < pairCount; i++) {
                const s = srcEntries[i];
                const d = destEntries[i];
                if (!this._areValuesEqual(s.value, d.value)) {
                    mismatches.push({
                        sourcePath: s.rawPath,
                        destinationPath: d.rawPath,
                        mappedSourceRule: srcRule,
                        mappedDestinationRule: destRule,
                        sourceValue: s.value,
                        destinationValue: d.value,
                        anomalyCategory: 'Field Level Anomaly',
                        anomalyType: 'Value Mismatch'

                    });
                    errorMessages.push(`Value mismatch ${s.rawPath} -> ${d.rawPath}`);
                }
            }
        }
        const isValid =
            missingSource.length === 0 &&
            missingDestination.length === 0 &&
            mismatches.length === 0;

        return {
            isValid,
            errorMessages,
            missingSource,
            missingDestination,
            mismatches
        };
    },

    _normalizeRulePath(p: string) {
        return p
            .replace(/<array\d+>/g, '<array>')
            .replace(/\?/g, '')
            .replace(/^\./, '');
    },

    _parseOverrideExpr(expr: string): { kind: 'compare' | 'exists'; op?: '==' | '!='; path: string; value?: unknown; negate?: boolean } | null {
        expr = expr.trim();
        // exists(path) / !exists(path)
        const existsMatch = /^(?:!?\s*exists)\(\s*([^)]+)\s*\)$/.exec(expr.replace(/\s+/g, ''));
        if (existsMatch) {
            const raw = expr.replace(/\s+/g, '');
            const negate = raw.startsWith('!exists');
            return { kind: 'exists', path: existsMatch[1], negate };
        }
        const cmp = /^(.+?)(==|!=)(.+)$/.exec(expr);
        if (!cmp) return null;
        const [, left, op, rightRaw] = cmp.map(s => s.trim());
        let val: unknown = rightRaw;
        if (/^".*"$/.test(rightRaw) || /^'.*'$/.test(rightRaw)) {
            val = rightRaw.slice(1, -1);
        } else if (/^(true|false)$/i.test(rightRaw)) {
            val = rightRaw.toLowerCase() === 'true';
        } else if (/^-?\d+(\.\d+)?$/.test(rightRaw)) {
            val = Number(rightRaw);
        }
        return { kind: 'compare', op: op as '==' | '!=', path: left, value: val };
    },

    _evaluateOverrideExpr(override: RuleOverrideExpression, grouped: Record<string, { rawPath: string; value: unknown }[]>, normalize: (p: string) => string): boolean {
        const parsed = this._parseOverrideExpr(override.expr);
        if (!parsed) return false;
        if (parsed.kind === 'exists') {
            const norm = normalize(parsed.path);
            const hits = grouped[norm];
            const exists = !!(hits && hits.some(h => h.value !== null && h.value !== undefined && h.value !== ''));
            return parsed.negate ? !exists : exists;
        }
        if (parsed.kind === 'compare') {
            const norm = normalize(parsed.path);
            const hits = grouped[norm] || [];
            if (parsed.op === '==') {
                return hits.some(h => h.value === parsed.value);
            }
            if (parsed.op === '!=') {
                return hits.every(h => h.value !== parsed.value);
            }
        }
        return false;
    },

    _resolveRuleDefinition(def: RuleDefinition, grouped: Record<string, { rawPath: string; value: unknown }[]>, normalize: (p: string) => string): { regex: RegExp; optional: boolean } {
        if (def instanceof RegExp) {
            return { regex: def, optional: false };
        }
        const obj: ExtendedRuleObject = def;
        // Base
        let effectiveRegex = obj.regex;
        let effectiveOptional = obj.required ? false : !!obj.optional;

        if (obj.overrides && obj.overrides.length) {
            for (const ov of obj.overrides) {
                if (this._evaluateOverrideExpr(ov, grouped, normalize)) {
                    if (ov.set?.regex) effectiveRegex = ov.set.regex;
                    if (ov.set?.required === true) effectiveOptional = false;
                    else if (ov.set?.optional !== undefined) effectiveOptional = ov.set.optional;
                    // First match wins
                    break;
                }
            }
        }
        return { regex: effectiveRegex, optional: effectiveOptional };
    }
}
