﻿// Shared integration validation type definitions and (now) shared wrapper validator registry.
// Import this in orchestrator (index.ts) and any downstream modules.

import { Validators } from './validators.js';
import { AnomalyCategory, AnomalyType, SoxErrorDefinition } from './error-definitions.js';


// -------------------- Types --------------------
export type FieldPathMap = Record<string, string>;

export interface ValidatePairParams {
  sourceIntegrationId: string;
  destinationIntegrationId: string;
  sourcePayload: unknown;
  destinationPayload: unknown;
}

export interface ValidateIntegrationParams {
  sourceIntegrationId: string;
  payload: any;
}

export interface PayloadRuleValidationResult {
  isValid: boolean;
  errorMessages: string[];
  failures: { rulePath: string; actualPath: string; value: unknown; anomalyCategory: AnomalyCategory, anomalyType: AnomalyType }[];
}

export interface MappingComparisonResult {
  isValid: boolean;
  errorMessages: string[];
  missingSource: string[];
  missingDestination: string[];
  mismatches: {
    sourcePath: string;
    destinationPath: string;
    mappedSourceRule: string;
    mappedDestinationRule: string;
    sourceValue: unknown;
    destinationValue: unknown;
    anomalyCategory: AnomalyCategory,
    anomalyType: AnomalyType;
  }[];
}
 

export interface IntegrationValidationResult {
  sourceIntegrationId: string;
  destinationIntegrationId: string;
  sourceValidation?: PayloadRuleValidationResult;
  destinationValidation?: PayloadRuleValidationResult;
  mappingComparison?: MappingComparisonResult | null;
  isValid: boolean;
  errors: string[];
}

export interface SingleIntegrationValidationResult {
  sourceIntegrationId: string;
  destinationIntegrationId?: string;
  sourceValidation?: PayloadRuleValidationResult;
  destinationValidation?: null;
  mappingComparison?: MappingComparisonResult | null;
  isValid: boolean;
  errors: string[];
}

// -------------------- Wrapper Validator Registry --------------------
// (Moved from index.ts)

// Type
export type WrapperValidatorFn = (value: unknown) => {
    isValid: boolean;
    errorMessages: string[];
};

// API → HttpSoxKey
const httpSoxKeys = [
    'int11-2', // API    
    'int12-2', // API    
    'int08-1', // API
    'int09-1', // API
    'int10-1', // API
    'int16',   // API/API
    'int17',   // API/DB
    'int25',   // API/API
    'int26',   // API/DB
    'int21',   // API/API
    'int22',   // API/API
    'int11',
    'int11-1' // DB

] as const;

// EH/DB → AsyncSoxKey
const asyncSoxKeys = [
    'int03-1', // EH/EH
    'int04',   // EH/EH, EH/DB
    'int03-2', // EH/EH
    'int31',   // EH/DB
    'int12-1', // DB
    'int15-1-1', // EH/EH
    'int15-2-2', // EH/EH
    'int15-2-1', // EH/EH
    'int15-3-2', // EH/EH
    'int15-3-1', // EH/EH
    'int27',     // EH/EH
    'int28',     // EH/EH
    'int29',     // DB
    'int30',     // DB 
    'int32-2',   // EH/EH
    'int32-1',   // EH/EH
    'int33-2',   // EH/EH
    'int33-1',   // EH/EH
    'int24-1',   // EH/DB
    'int18',     // DB
    'int19-1',   // DB/DB
    'int19-2',   // DB/DB
    'int19-3',   // DB/DB
    'int20'      // API/DB
] as const;

// ---- Strong typing ----
type AsyncSoxKey = typeof asyncSoxKeys[number];
type HttpSoxKey = typeof httpSoxKeys[number];
export type WrapperValidatorKey = AsyncSoxKey | HttpSoxKey;

// ---- Registry ----
export const WRAPPER_VALIDATOR_REGISTRY: Record<WrapperValidatorKey, WrapperValidatorFn> = {
    ...Object.fromEntries(asyncSoxKeys.map(key => [key, Validators.validateAsyncSoxWrapper])),
    ...Object.fromEntries(httpSoxKeys.map(key => [key, Validators.validateHttpSoxWrapper]))
} as Record<WrapperValidatorKey, WrapperValidatorFn>;

export interface RuleOverrideExpression {
  /**
   * Lightweight expression language:
   *   path==true
   *   path==123
   *   path=="ABC"
   *   path!=0
   *   exists(path.to.field)
   *   !exists(path.to.field)
   *
   * path may include <array> placeholders. Whitespace is ignored around operators.
   */
  expr: string;
  /**
   * Settings applied if expr evaluates truthy.
   * optional / required: optional precedence logic: required=true => optional=false.
   * regex: optional override pattern.
   */
  set?: {
    optional?: boolean;
    required?: boolean;
    regex?: RegExp;
  };
  message?: string; // Optional custom message appended on activation.
}

export interface ExtendedRuleObject {
  regex: RegExp;
  optional?: boolean;
  required?: boolean;
  overrides?: RuleOverrideExpression[]; // First matching override (in order) wins.
}

// Unified rule definition union.
// Existing plain RegExp entries remain valid.
export type RuleDefinition = RegExp | ExtendedRuleObject;

// Backwards compatible map
export type RuleMap = Record<string, RuleDefinition>;