﻿export type DynatraceResult = {
    types?: Array<{
        mappings?: Record<string, { type: string }>;
        indexRange?: [number, number];
    }>;
    records?: DynatraceRecord[];
    metadata?: DynatraceMetadata;
};
export type AlarmDoc = {
    alarmCode: string;
    miTeam: string;
    dynatraceDashboardUrl: string;
    alarmDescription: string;
    recordsText: string;
};

type DynatraceRecord = {
    "Errors": string | number | null;
    "Total Records": string | number | null;
    "Error Percentage": number | string | null;
    "Source Integration": string | null;
    "Destination Integration": string | null;
};
type DynatraceMetadata = {
    grail: DynatraceGrailMetadata;
};

type NormalizedRow = {
    "Source Integration": string | null;
    "Destination Integration": string | null;
    Errors: string;
    "Total Records": string;
    "Error Percentage": number | null;
};
type DynatraceGrailMetadata = {
    query: string;
    locale: string;                 // e.g. "und"
    queryId: string;                // UUID
    sampled: boolean;
    timezone: string;               // e.g. "Z"
    dqlVersion: string;             // e.g. "V1_0"
    scannedBytes: number;
    notifications: unknown[];       // empty array in sample; structure not specified
    canonicalQuery: string;
    scannedRecords: number;
    analysisTimeframe: DynatraceGrailAnalysisTimeframe;
    scannedDataPoints: number;
    executionTimeMilliseconds: number;
};
export type DynatraceGrailAnalysisTimeframe = {
    end: string;   // ISO8601 timestamp, e.g. "2025-09-23T17:45:00.000Z"
    start: string; // ISO8601 timestamp, e.g. "2025-09-23T16:45:00.000Z"
};


export function getReportAlarmData(dtResult: DynatraceResult, dynatraceDashboardUrl: string): AlarmDoc[] {
    const recordsRaw: DynatraceRecord[] = Array.isArray(dtResult?.records) ? dtResult.records! : [];
    const merged = mergeMissingDest(recordsRaw);
    const soxAlarmMap = getsoxAlarmMap();
    const mappedArray = mapRowsToSox(dtResult, merged, soxAlarmMap, dynatraceDashboardUrl);
    return mappedArray; // ← array of objects, each with a printable string
}

function mergeMissingDest(rows: DynatraceRecord[]): NormalizedRow[] {
    const validSingleIntegrations = ["INT08-1", "INT09-1", "INT10-1"];

    const toNum = (v: unknown) => (v == null || v === "" ? 0 : Number(v));
    const round2 = (n: number) => (Number.isFinite(n) ? Number(n.toFixed(2)) : n);
    const same = (a?: string | null, b?: string | null) =>
        String(a ?? "").toLowerCase() === String(b ?? "").toLowerCase();

    const out: NormalizedRow[] = [];
    // Reusable single-line row adder
    const addRow = (
        src: string | null,
        dst: string | null,
        err: number | string,
        tot: number | string,
        pct: number | null
    ) =>
        out.push({
            "Source Integration": src,
            "Destination Integration": dst,
            Errors: String(err),
            "Total Records": String(tot),
            "Error Percentage": pct
        });

    const missingTransactionRows: Record<string, number> = {};

    for (const r of rows) {
        const src = r["Source Integration"] || "-";
        const dst = r["Destination Integration"] || "-";
        const errors = toNum(r["Errors"]);
        const total = toNum(r["Total Records"]);
        const errorPct = round2(toNum(r["Error Percentage"]));

        // Rows with both src and dst as "-" its invalid data, all treated as errors
        if ((!src || src === "-") && (!dst || dst === "-")) {
            addRow("Log Schema Mismatch", "Log Schema Mismatch", total, total, total);
            continue;
        }
        // process only integrations with valid source after this
        if (!src || src === "-") continue;

        // Single integrations
        if (validSingleIntegrations.some((id) => same(id, src))) {
            addRow(src, "-", errors, total, errorPct);
            continue;
        }
        // Normal source->destination rows
        if (dst !== "-") {
            addRow(src, dst, errors, total, errorPct);
        } else {
            const tot = toNum(r["Total Records"]);
            missingTransactionRows[src] = (missingTransactionRows[src] || 0) + tot;
        }
    }
    // Apply missing totals as error bump
    for (const src of Object.keys(missingTransactionRows)) {
        const bump = missingTransactionRows[src] || 0;
        if (bump <= 0) continue;

        const match = out.find((o) => same(o["Source Integration"], src));
        if (match) {
            const baseErr = toNum(match.Errors);
            const baseTot = toNum(match["Total Records"]);
            const newErr = baseErr + bump;
            const newTot = baseTot + bump;
            match.Errors = String(newErr);
            match["Total Records"] = String(newTot);
            match["Error Percentage"] = newTot ? round2((newErr * 100) / newTot) : null;
        } else {
            addRow(src, "-", bump, bump, 100);
        }
    }
    return out;
}

function mapRowsToSox(
    dtResult: DynatraceResult,
    rows: NormalizedRow[],
    soxAlarmMap: Record<string, { integrations: string[]; miTeam: string }>,
    dynatraceDashboardUrl: string
): AlarmDoc[] {
    const norm = (s: unknown) => (s == null ? "" : String(s).trim().toUpperCase());

    const alarmSets = Object.fromEntries(
        Object.entries(soxAlarmMap).map(([code, v]) => [code, new Set((v.integrations || []).map(norm))])
    );

    // Accumulator of raw rows per alarm
    const grouped: Record<string, NormalizedRow[]> = Object.fromEntries(
        Object.keys(soxAlarmMap).map((k) => [k, [] as NormalizedRow[]])
    );

    for (const r of rows) {
        const src = norm(r["Source Integration"]);
        const dst = norm(r["Destination Integration"]);

        // SOX001 gets all rows
        grouped.SOX001.push(r);

        // Others: match by source/destination
        for (const code of Object.keys(soxAlarmMap)) {
            if (code === "SOX001") continue;
            const set = alarmSets[code];
            if (!set || set.size === 0) continue;
            if ((src && set.has(src)) || (dst && set.has(dst))) {
                grouped[code].push(r);
            }
        }
    }

    // Helpers for formatting
    const toNum = (v: unknown) => (v == null || v === "" ? 0 : Number(v));
    const round2 = (n: number) => (Number.isFinite(n) ? Number(n.toFixed(2)) : n);
    const pctStr = (num: number | null) => (num == null ? "0" : num.toFixed(2));

    // Format helper: one line per row (used for non-SOX001 alarms)
    const line = (r: NormalizedRow) => {
        const src = r["Source Integration"] ?? "";
        const dst = r["Destination Integration"] ?? "";
        const err = r.Errors ?? 0;
        const tot = r["Total Records"] ?? 0;
        const pct = r["Error Percentage"] != null ? Number(r["Error Percentage"]).toFixed(2) : "n/a";
        // Show destination only when it's not "-" or null
        const path = dst && dst !== "-" ? `${src}→${dst}` : `${src}`;
        return `${path} |Errors: ${err}/${tot} (${pct}%)`;
    };

    return Object.entries(soxAlarmMap).map(([code, meta]) => {
        const rowsForAlarm = grouped[code] || [];

        // Special aggregated view for SOX001 only
        if (code === "SOX001") {
            const isMismatch = (r: NormalizedRow) =>
                (r["Source Integration"]?.toLowerCase() === "log schema mismatch") ||
                (r["Destination Integration"]?.toLowerCase() === "log schema mismatch");

            const mismatchRows = rowsForAlarm.filter(isMismatch);
            const normalRows = rowsForAlarm.filter(r => !isMismatch(r));

            const aggregate = (list: NormalizedRow[]) => {
                const totals = list.reduce(
                    (acc, r) => {
                        acc.err += toNum(r.Errors);
                        acc.tot += toNum(r["Total Records"]);
                        return acc;
                    },
                    { err: 0, tot: 0 }
                );
                const pct = totals.tot ? round2((totals.err * 100) / totals.tot) : null;
                return { ...totals, pct };
            };

            const normalAgg = aggregate(normalRows);
            const mismatchAgg = aggregate(mismatchRows);

            const recordsText =
                `Total Transactions |Errors: ${normalAgg.err}/${normalAgg.tot} (${pctStr(normalAgg.pct)}%)` +
                `|Log Schema Mismatch |Errors: ${mismatchAgg.err}/${mismatchAgg.tot} (${pctStr(mismatchAgg.pct)}%)`;

            const alarmDescription = formatSoxAnomalyReportPeriod(dtResult, code);
            return {
                alarmCode: code,
                miTeam: meta.miTeam,
                dynatraceDashboardUrl,
                alarmDescription,
                recordsText
            };
        }

        // Default behavior for all other SOX alarms
        const body = rowsForAlarm.length ? rowsForAlarm.map(line).join("|") : "(no records)";
        const recordsText = `${body}`;
        const alarmDescription = formatSoxAnomalyReportPeriod(dtResult, code);
        return {
            alarmCode: code,
            miTeam: meta.miTeam,
            dynatraceDashboardUrl,
            alarmDescription,
            recordsText
        };
    });
}

function getsoxAlarmMap(): Record<string, { integrations: string[]; miTeam: string }> {
    return {
        SOX001: { integrations: ["--"], miTeam: "Period Aggregate - Finance" },
        SOX002: {
            integrations: ["INT03-1", "INT03-2", "INT04", "INT31", "INT12-2", "INT32-2", "INT15-3-2", "INT33-2"],
            miTeam: "HotepOps, Hints, Fods"
        },
        SOX003: { integrations: ["INT11-2", "INT15-2-2", "INT25", "INT26", "INT30"], miTeam: "Res" },
        SOX004: {
            integrations: [
                "INT15-1-1",
                "INT15-2-1",
                "INT15-3-1",
                "INT16",
                "INT17",
                "INT18",
                "INT20",
                "INT19-1",
                "INT19-2",
                "INT19-3"
            ],
            miTeam: "Loyalty S&E"
        },
        SOX005: { integrations: ["INT28", "INT29"], miTeam: "Loyalty Foundations" },
        SOX006: { integrations: ["INT22"], miTeam: "Loyalty Redeem" },
        SOX007: { integrations: ["INT08-1", "INT09-1", "INT10-1", "INT27", "INT28"], miTeam: "Product Catalog" },
        SOX008: { integrations: ["INT11", "INT12-1", "INT32-1", "INT33-1"], miTeam: "Finance" },
        SOX009: { integrations: ["INT24-1"], miTeam: "MDP" },
        SOX010: { integrations: ["INT21"], miTeam: "Shop & Book" }
    };
}

function formatSoxAnomalyReportPeriod(dtResult: DynatraceResult, alarmCode: String): string {
    const timeframe = dtResult?.metadata?.grail?.analysisTimeframe;

    const formatIsoAsMMDDYYYY = (iso: string): string => {
        const d = new Date(iso);
        if (Number.isNaN(d.getTime())) return iso; // fallback to raw if unparsable
        const mm = String(d.getUTCMonth() + 1).padStart(2, "0");
        const dd = String(d.getUTCDate()).padStart(2, "0");
        const yyyy = d.getUTCFullYear();
        return `${mm}/${dd}/${yyyy}`;
    };

    if (!timeframe?.start || !timeframe?.end) {
        return `${alarmCode} | Sox Anomaly Report`;
    }

    const start = formatIsoAsMMDDYYYY(timeframe.start);
    const end = formatIsoAsMMDDYYYY(timeframe.end);
    return `${alarmCode} | Sox Anomaly Analysis Reporting Period ${start} to ${end}`;
}